import React, {useState} from 'react'
import styled from 'styled-components'
import SideBar from '../components/sidebar/SideBar'
import NavBar from '../components/nav/PagesNavBar'
import { Heading, Subtitle, TopLine, InfoContainer, InfoWrapper, InfoCard, Text, TextWrapper } from '../components/info_section/InfoElems'
import { FaqWrapper } from '../components/faq_section/FaqElems'
import Footer from '../components/footer/Footer'
import Faq from 'react-faq-component'
import {data2} from '../data/Data'
import icon from '../media/city2.svg'

const Container = styled.div`
  background: #fafafa;
  align-items: center;
  justify-content: center;
  display: flex;
  padding-bottom: 50px;
`
const Img = styled.img`
    height: 200px;
    margin-top: 20px;
    margin-bottom: 30px;
`

const AboutPage = () => {
    const [isOpen, setIsOpen] = useState(false);
  
    const toggle = () => {
      setIsOpen(!isOpen)
    };

  return (
    <>
    <SideBar isOpen={isOpen} toggle={toggle}/>
    <NavBar toggle={toggle}/>
    <div style={{"height": "60px", "background":"#fafafa"}}/>
    <InfoContainer id='help' color={"#fafafa"}>
        <Heading>Sobre Nós 🏡</Heading>
        <div style={{"height": "30px"}}/>
        <TextWrapper>
        <Text>A construção é o setor que produz mais resíduos em Portugal e, por isso, pretendemos trazer uma maior sustentabilidade 
          ao utilizar esses mesmos resíduos na produção de novos materiais de construção, dando-lhes os devido valor. 
          Comprometemo-nos em manter esse o nosso foco: construir um futuro melhor.
          <br />
          <br />
          Contacte-nos através de:<br />
          wastewiseinnovations@gmail.com
        </Text>
        </TextWrapper>
    </InfoContainer>
    <div style={{"display":"flex", "background":"#fafafa", "align-items":"center", "justify-content":"center"}}>
      <Img src={icon} />
    </div>
    <Footer />
    </> 
  )
}

export default AboutPage